<template>
    <div id="cheeru-paystep">
    <div class="mask" v-show="showmask || showmask2 || showmask3 || showmask4" @click="hidemask" id="mask"></div>
    <transition name="fade">
    	<div class="infobox infoboxExtra	" v-if="showmask4">
    		<i class="el-icon-close" @click="hidemask"></i>
    		<p>出发前45天(不含)以上买家通知取消，收取订单75%的费用出发前45天以内买家通知取消，收取订单100%的费用; 注：如按上述比例支付的业务损失费不足以赔偿卖家的实际损失，买家应当按实际损失对卖家予以赔偿，但最高额不应当超过订单费用总额。					</p>
    	</div>
    </transition>
    <transition name="fade">
    	<div class="infobox" v-if="showmask3">
    		<i class="el-icon-close" @click="hidemask"></i>
    		<div class="stepone">
					1.修改联系人信息
				</div>
				<div class="stepone_form">
					<div class="onetips">* 用于订单确认及预订过程中的相关事项联系。</div>
					<input type="text" placeholder="中文姓名" class="oneform" v-model="name">
					<input type="text" placeholder="手机号码" class="oneform" maxlength="11" v-model="mobile">
					<input type="text" placeholder="微信号" class="oneform" v-model="wechat">
					<div class="nextbtn" @click="modifyconfirm" v-if="status < 4">保存</div>
				</div>
    	</div>
    </transition>
    <transition name="fade">
    	<div class="infobox" v-if="showmask">
    		<i class="el-icon-close" @click="hidemask"></i>
		    <p class="infoTitle">第{{temnum}}位客户资料</p>
		    <div class="infoTips">
		    	<span>* 必填项</span>
		    	<div class="line"></div>
		    </div>
		    <div class="form_item">
					<div class="inputbox">
						<p class="subtitle"><span>中文姓名</span><span class="extraspan">英文姓名</span></p>
						<input type="text" class="firstinput" placeholder="请输入中文姓名" v-model="cname">
						<input type="text" placeholder="请输入英文姓名" v-model="ename">
						<div class="clear"></div>
					</div>
				</div>
				<div class="form_item">
					<div class="inputbox">
						<p class="subtitle"><span>护照号</span><span class="extraspan">有效期</span></p>
						<input type="text" class="firstinput" placeholder="请输入护照号" v-model="passport">
						<input type="text" placeholder="请输入有效期" v-model="validity">
						<p class="subtitle"><span>国籍</span><span class="extraspan">签发地点</span></p>
						<input type="text" class="firstinput" placeholder="请输入国籍" v-model="nationality">
						<input type="text" placeholder="请输入签发地点" v-model="issueplace">
						<div class="clear"></div>
					</div>
				</div>
				<div class="infoTips">
		    	<span>* 选填项</span>
		    	<div class="line"></div>
		    </div>
		    <div class="form_item">
					<div class="inputbox">
						<p class="subtitle"><span>手机号</span><span class="extraspan">微信号</span></p>
						<input type="text" class="firstinput" placeholder="请输入手机号" maxlength="11" v-model="themobile">
						<input type="text" placeholder="请输入微信号" v-model="thewechat">
						<div class="clear"></div>
					</div>
				</div>
				<div class="btn_group">
					<button class="save_btn" @click="saveinfo">保存</button>
					<button class="add_btn" @click="open2">使用已有的常用人</button>
				</div>
    	</div>
    </transition>
    <transition name="fade">
    	<div class="infobox2" v-if="showmask2">
    		<i class="el-icon-close" @click="hidemask"></i>
    		<div class="people_item" v-for="(item, index) in alwaysPeople">
    			<p>{{item.trip_person_name}}的信息</p>
    			<button @click="use(item, index)" v-if="isuseArray[index].isuse===false">使用</button>
    			<span v-else>已使用该常用人信息</span>
    		</div>
				<!-- <el-pagination
          small
          layout="prev, pager, next"
          :page-count='5'
          :total="30"
          @current-change="handleCurrentChange">
         </el-pagination> -->
    	</div>
		</transition>
	  	<!-- 导航 -->
		<cheeru-banner type="white" :index="4"></cheeru-banner>
		<div class="paycontent">
			<div class="pay_l">
				<div class="stepone">
					1.填写联系人信息
					<button class="modify" v-if="status !== 1 && status <= 3" @click="modifyone">修改</button>
					<button class="modify" v-if="status > 3" @click="modifyone">查看</button>
				</div>
				<div class="stepone_form" v-if="status === 1">
					<div class="onetips">* 用于订单确认及预订过程中的相关事项联系。</div>
					<input type="text" placeholder="中文姓名" class="oneform" v-model="name">
					<input type="text" placeholder="手机号码" class="oneform" maxlength="11" v-model="mobile">
					<input type="text" placeholder="微信号" class="oneform" v-model="wechat">
					<div class="nextbtn" @click="gotwo">下一步</div>
				</div>
				<div class="line" v-if="status !== 1"></div>
				<div class="steptwo">
					<p v-if="status === 2 && issubmit === false">2.选择付款方式</p>
					<p v-if="status >= 2 && issubmit === true">2.付款方式 : <span v-if="orderInfo.pay_status==='1'">支付宝支付</span><span v-if="orderInfo.pay_status==='2'">微信支付</span></p>
					<button class="modify" v-if="status === 2 && issubmit === true" @click="backtwo">更改</button>
					<span class="payok" v-if="status > 2">
						<img src="./images/payok.png" alt="">
					</span>
				</div>
				<div class="line" v-if="status > 2"></div>
				<div class="stepthree" v-if="status >= 3">
					<p v-if="customers - tripPerson.length > 0">3.出行客户资料填写，剩{{customers - tripPerson.length}}人未完善。</p>
					<p v-else>3.出行客户资料填写，已完成。</p>
					<span>*客户资料用于确认出行人信息</span>
					<div class="customer_box">
						<div class="customer_item" v-for="(item, index) in tripPerson">
							<p>{{item.customer_name}} , 信息已完善.</p>
						</div>
						<div class="customer_item" v-for="(n, index) in customers - tripPerson.length">
							<p>第 {{n + tripPerson.length}} 位客户资料，未完善。</p>
							<button @click="openbox(n, index)">填写</button>
						</div>
					</div>
				</div>
				<div class="paytype" v-if="status === 2 && issubmit === false">
					<div class="paytype_item" @click="chooseone">
						<div class="type_logo">
							<img src="./images/type1.png" alt="">
						</div>
						<div class="type_name" :class="{ choosename: paystatus === 1}">使用支付宝支付</div>
						<span class="type_choose" v-if="paystatus !== 1"></span>
						<span class="type_ischoose" v-if="paystatus === 1">
							<span class="inset"></span>
						</span>
					</div>
					<div class="paytype_item" @click="choosetwo">
						<div class="type_logo">
							<img src="./images/type2.png" alt="">
						</div>
						<div class="type_name" :class="{ choosename: paystatus === 2}">使用微信支付</div>
						<span class="type_choose" v-if="paystatus !== 2"></span>
						<span class="type_ischoose" v-if="paystatus === 2">
							<span class="inset"></span>
						</span>
					</div>
				</div>
				<div class="agreechoose" v-on:click.self="chooseagree" v-if="status === 2 && issubmit === false">
					<span class="type_choose" v-if="agreeit === false" v-on:click.self="chooseagree"></span>
					<span class="type_ischoose" v-if="agreeit === true">
						<span class="inset"></span>
					</span>
					<span class="agreefile" v-on:click.self="chooseagree">本人已经认真阅读<span @click.stop="agreeinset">《退改签协议》</span>的内容并同意上述条款。</span>
				</div>
				<button class="confirmBtn" @click="confirmtwo" v-if="status === 2 && issubmit === false">确 认</button>
				<div class="payimage" v-if="status === 2 && issubmit === true">
					<div class="QRcode" v-if="showerror===false">
						<qrcode 
						:value="qrcodeUrl" 
						v-if="qrcodeUrl" 
						:options="{ size: 260 }">
						</qrcode>
					</div>
					<div class="QRtips" v-if="showerror===false">请通过扫描二维码支付，如果已经支付成功，未跳转请<span @click="refresh">点击刷新</span></div>
					<div class="QRcode" v-if="showerror===true">
						<i class="el-icon-close"></i>
					</div>
					<div class="QRtips" v-if="showerror===true">生成二维码失败,请点击按钮重试<span @click="refresh">点击重试</span></div>
				</div>
			</div>
			<div class="pay_r">
				<div class="showimg">
					<img :src="'http://price.cheeruislands.com/uploads/file/' + orderInfo.image_path" alt="" v-if="orderInfo">
				</div>
				<p class="routetitle">{{orderInfo.order_name}}</p>
				<div class="itembox">
					<div class="itembar">
						<div class="itembar_l">出行日期</div>
						<div class="itembar_r">{{orderInfo.trip_date}}</div>
						<div class="clear"></div>
					</div>
					<div class="itembar">
						<div class="itembar_l">成人数量</div>
						<div class="itembar_r"> * {{orderInfo.adult_number}}</div>
						<div class="clear"></div>
					</div>
					<div class="itembar">
						<div class="itembar_l">儿童数量</div>
						<div class="itembar_r"> * {{orderInfo.child_number}}</div>
						<div class="clear"></div>
					</div>
					<div class="itembar">
						<div class="itembar_l">房间数量</div>
						<div class="itembar_r">* {{orderInfo.room_number}}</div>
						<div class="clear"></div>
					</div>
				</div>
				<div class="totalmoney">
					<div class="itembar_l">总价</div>
					<div class="itembar_r">{{orderInfo.update_total_price}}</div>
					<div class="clear"></div>
				</div>
				<div class="tips">
					<span v-if="orderInfo.route_type===1">* < 该产品为机 + 酒店 + 往返交通产品 ></span>
					<span v-if="orderInfo.route_type===2">* < 该产品为酒店 + 往返交通产品 ></span>
					<span v-if="orderInfo.route_type===3">* < 该产品为包 酒店 交通产品 ></span>
				</div>
			</div>
			<div class="clear"></div>
		</div>
    </div>
</template>

<script>
import Qrcode from 'vue-qrcode'
import banner from '../../components/common/banner'
import footer from '../../components/common/footer'
import { getRoutePlainFare, getDescAllInfo, getOrderLinkmanInfo, updateLinkmanInfo, getTripPersonList, updateCustomerInfo, getPersonList, getPersonInfo, qrcodePay, query, updatePayStatus, updateOrderPay } from '../../api/api'
export default {
	data() {
		return {
			qrcodeUrl: '',
			showerror: false,
			isuseArray: [],
			default_token: '',
			alwaysPeople: [],
			temorderid: '',
			tripPerson: [],
			default_accid: '',
			themobile: '',
			orderInfo: '',
			thewechat: '',
			cname: '',
			ename: '',
			passport: '',
 			customers: 6,
			status: '',
			nationality: '',
			validity: '',
			name: '',
			issueplace: '',
			mobile: '',
			showmask: false,
			wechat: '',
			paystatus: '',
			issubmit: false,
			agreeit: false,
			showmask2: false,
			showmask3: false,
			showmask4: false,
			temnum: ''
		}
	},
	components: {
		'cheeru-banner': banner,
		'cheeru-footer': footer,
		'qrcode': Qrcode
	},
	created() {
		if (this.getCookie('login_type') !== '') {
			if (sessionStorage.getItem(this.encryption('order'))) {
				let info = {
					order_id: this.decrypt(sessionStorage.getItem(this.encryption('order')))
				};
				this.getOrderLinkmanInfo(info);
			} else {
				window.location.href = 'product.html';
			}
		} else {
			let cookie = sessionStorage.getItem(this.encryption('info'));
			if (!cookie) {
				window.location.href = 'product.html';
			} else {
				if (sessionStorage.getItem(this.encryption('order'))) {
					let info = {
						order_id: this.decrypt(sessionStorage.getItem(this.encryption('order')))
					};
					this.getOrderLinkmanInfo(info);
			} else {
				window.location.href = 'product.html';
			}
			}
		}
	},
	mounted() {
		this.resetHeight();
		window.onresize = () => {
			this.resetHeight();
		}
		if (sessionStorage.getItem(this.encryption('info'))) {
			this.default_accid = this.decrypt(JSON.parse(sessionStorage.getItem(this.encryption('info'))).www);
			this.default_token = this.decrypt(JSON.parse(sessionStorage.getItem(this.encryption('info'))).xxx);
		}
	},
	watch: {
		showmask() {
			if (this.showmask === true) {
				window.scroll(0, 0);
				document.body.style.overflowX = 'hidden';
			} else {
			}
		},
		showmask2() {
			if (this.showmask2 === true) {
				window.scroll(0, 0);
				document.body.style.overflowX = 'hidden';
			} else {
			}
		}
	},
	methods: {
		getOrderLinkmanInfo(info) {
			getOrderLinkmanInfo(info).then((res) => {
				this.orderInfo = res.data;
				this.temorderid = this.orderInfo.id;
				this.status = this.orderInfo.order_status;
				this.customers = this.orderInfo.adult_number + this.orderInfo.child_number;
				if (this.status === 2) {
					if (this.orderInfo.pay_status === '0') {
						this.issubmit = false;
					} else {
						this.issubmit = true;
						// this.orderInfo.update_total_price
						let info4 = {
							subject: this.orderInfo.order_name,
							out_trade_no: this.orderInfo.order_pay_id,
							total_amount: 0.01
						};
						if (this.orderInfo.pay_status === '1') {
							info4.pay_type = '支付宝';
						} else if (this.orderInfo.pay_status === '2') {
							info4.pay_type = '微信';
						}
						qrcodePay(info4).then((res2) => {
							if (res2.data.status === 1) {
								this.showerror = false;
								this.issubmit = true;
								this.qrcodeUrl = res2.data.msg;
								let info2 = {
									out_trade_no: this.orderInfo.order_pay_id,
									customer_token: this.default_token,
									customer_id: this.default_accid
								};
								if (this.paystatus === 1) {
									info2.pay_type = '支付宝';
								} else if (this.paystatus === 2) {
									info2.pay_type = '微信';
								}
								query(info2).then((res3) => {
									if (res3.data.msg === '订单支付成功') {
										window.location.href = 'paystep.html';
									} else {
										this.queryit();
									}
								});
							} else if (res2.data.status === 0) {
								this.showerror = true;
							}
						});
					}
				}
				if (this.status > 2) {
					this.issubmit = true;
				}
				if (this.status >= 3) {
					let info = {
						order_id: this.temorderid
					};
					getTripPersonList(info).then((res) => {
						this.tripPerson = res.data;
						let info2 = {
							account_id: this.default_accid
						};
						getPersonList(info2).then((res) => {
							this.alwaysPeople = res.data;
							this.alwaysPeople.forEach((data) => {
								let info3 = {
									isuse: false
								};
								this.isuseArray.push(info3);
							});
							this.tripPerson.forEach((data, index) => {
								this.alwaysPeople.forEach((data2, index2) => {
									if (data.customer_name === data2.trip_person_name) {
										this.isuseArray[index2].isuse = true;
									}
								});
							});
						});
					});
				}
			});
		},
		getCookie(name) {
      if (document.cookie.length > 0) {
	        var start = document.cookie.indexOf(name + '=');
	        if (start !== -1) {
	        start = start + name.length + 1
	        var end = document.cookie.indexOf(';', start);
	        if (end === -1) end = document.cookie.length
	        return unescape(document.cookie.substring(start, end))
	        }
        }
        return ''
    },
		saveinfo() {
			if (this.cname === '') {
				this.$message('请输入中文姓名');
			} else if (this.ename === '') {
				this.$message('请输入英文姓名');
			} else if (this.passport === '') {
				this.$message('请输入护照号');
			} else if (this.validity === '') {
				this.$message('请输入护照有效期');
			} else if (this.nationality === '') {
				this.$message('请输入国籍');
			} else if (this.issueplace === '') {
				this.$message('请输入签发地点');
			} else if (this.themobile !== '' && !(/^1[34578]\d{9}$/.test(this.themobile))) {
				this.$message('请输入正确的手机号');
			} else {
				let info = {
					order_id: this.temorderid,
					customer_name: this.cname,
					customer_ename: this.ename,
					customer_passport: this.passport,
					validity_of_passport: this.validity,
					customer_nationality: this.nationality,
					place_of_issue: this.issueplace,
					customer_phone: this.themobile,
					customer_wechat: this.thewechat
				};
				let info2 = {'customer_info': info};
				updateCustomerInfo(info2).then((res) => {
					if (res.data === '修改成功') {
						this.$message({
							type: 'success',
							message: '操作成功'
						});
						setInterval(() => {
              window.location.href = 'paystep.html';
            }, 1000);
					}
				});
			}
		},
		modifyconfirm() {
			if (this.name === '') {
				this.$message('请输入姓名');
			} else if (this.mobile === '') {
				this.$message('请输入手机号');
			} else if (!(/^1[34578]\d{9}$/.test(this.mobile))) {
				this.$message('请填写正确的手机号');
			} else {
				let info = {
				id: this.temorderid,
				linkman_name: this.name,
				linkman_phone: this.mobile,
				linkman_wechat: this.wechat
			};
			let info2 = {'linkman_info': info};
			updateLinkmanInfo(info2).then((res) => {
				if (res.data === '修改成功') {
						this.$message({
							type: 'success',
							message: '修改成功'
						});
						setInterval(() => {
              window.location.href = 'paystep.html';
            }, 1000);
				}
			});
			}
		},
		modifyone() {
			this.showmask3 = true;
			this.name = this.orderInfo.linkman_name;
			this.mobile = this.orderInfo.linkman_phone;
			this.wechat = this.orderInfo.linkman_wechat;
		},
		handleCurrentChange(val) {
		},
		use(item, index) {
			let info = {
				person_id: item.id,
				acc_id: this.default_accid,
				token: this.default_token
			};
			getPersonInfo(info).then((res) => {
				this.cname = res.data.trip_person_name;
				this.ename = res.data.trip_person_ename;
				this.passport = res.data.trip_passport;
				this.validity = res.data.passport_date;
				this.nationality = res.data.nationality;
				this.issueplace = res.data.Place_of_issue;
				this.themobile = 	res.data.trip_person_phone;
				this.thewechat = 	res.data.wechat;
				this.showmask = true;
				this.showmask2 = false;
			});
		},
		resetHeight() {
				document.getElementById('mask').style.height = document.body.scrollHeight + 'px';
				document.getElementById('mask').style.width = document.body.scrollWidth + 'px';
		},
		open2() {
			this.showmask = false;
			this.showmask2 = true;
		},
		openbox(n, index) {
			this.temnum = n;
			this.showmask = true;
		},
		hidemask() {
			this.showmask = false;
			this.showmask2 = false;
			this.showmask3 = false;
			this.showmask4 = false;
		},
		backtwo() {
			this.issubmit = false;
		},
		refresh() {
			let info = {
				order_id: this.orderInfo.id
			};
			updateOrderPay(info).then((res) => {
				window.location.href = 'paystep.html';
			});
		},
		agreeinset() {
			this.showmask4 = true;
		},
		chooseagree() {
			this.agreeit = true;
		},
		// 第一步确认按钮
		gotwo() {
			if (this.name === '') {
				this.$message('请输入姓名');
			} else if (this.mobile === '') {
				this.$message('请输入手机号');
			} else if (!(/^1[34578]\d{9}$/.test(this.mobile))) {
				this.$message('请填写正确的手机号');
			} else {
				let info = {
					id: this.temorderid,
					linkman_name: this.name,
					linkman_phone: this.mobile,
					linkman_wechat: this.wechat
				};
				let info2 = {'linkman_info': info}
				updateLinkmanInfo(info2).then((res) => {
					if (res.data === '修改成功') {
						this.$message({
							type: 'success',
							message: '操作成功'
						});
						setInterval(() => {
              window.location.href = 'paystep.html';
            }, 1000);
					}
				});
			}
		},
		// 第二步确认按钮
		confirmtwo() {
			if (this.paystatus === '') {
				this.$message('请选择支付方式');
			} else if (this.agreeit === false) {
				this.$message('请阅读协议内容');
			} else {
				let info3 = {
				  pay_status: this.paystatus,
				  order_id: this.orderInfo.id
				};
				updatePayStatus(info3).then((res) => {
					if (res.data === '修改成功') {
						// this.orderInfo.update_total_price
						let info = {
							subject: this.orderInfo.order_name,
							out_trade_no: this.orderInfo.order_pay_id,
							total_amount: 0.01
						};
						if (this.paystatus === 1) {
							info.pay_type = '支付宝';
						} else if (this.paystatus === 2) {
							info.pay_type = '微信';
						}
						qrcodePay(info).then((res2) => {
							if (res2.data.status === 1) {
								this.showerror = false;
								let info5 = {
									order_id: this.decrypt(sessionStorage.getItem(this.encryption('order')))
								};
								this.getOrderLinkmanInfo(info5);
								this.issubmit = true;
								this.qrcodeUrl = res2.data.msg;
								this.queryit();
							} else if (res2.data.status === 0) {
								this.showerror = true;
							}
						});
					} else if (res.data === '修改失败') {
						window.location.href = 'paystep.html';
					}
				});
			}
		},
		queryit() {
			let info2 = {
				out_trade_no: this.orderInfo.order_pay_id,
				customer_token: this.default_token,
				customer_id: this.default_accid
			};
			if (this.paystatus === 1) {
				info2.pay_type = '支付宝';
			} else if (this.paystatus === 2) {
				info2.pay_type = '微信';
			}
			query(info2).then((res2) => {
				if (res2.data.msg === '订单支付成功') {
					window.location.href = 'paystep.html';
				} else {
					this.queryit();
				}
			});
		},
		chooseone() {
			this.paystatus = 1;
		},
		choosetwo() {
			this.paystatus = 2;
		}
	}
}
</script>

